import React from "react";
import {
  Jumbotron
} from "reactstrap";
import BootstrapTable from "react-bootstrap-table-next";

export class User extends React.Component {
  constructor(props) {
    super(props);
  }  

  componentDidMount() {
    fetch('./server/routes/users') 
      .then(res => res.json())
      .then(users => this.setState({ users }));
  }

  render() {
   let state = {users: []}
    let columns = [
      {
        dataField: "id",
        text: "User ID"
      },
      {
        dataField: "name",
        text: "User Name",
      },
      {
        dataField: "type",
        text: "User Type"
      }
    ];

    return (
      <div>
        <Jumbotron>
          <h1 className="display-4">Users</h1>
          <br/>
          <BootstrapTable
            keyField="id"
            data={users}
            columns={columns}
            striped
            hover
            condensed
          />
        </Jumbotron>
        <h1>Users</h1>
        {this.state.users.map(user =>
          <div key={user.id}>{user.username}</div>
        )}
      </div>
    );
  }
}



